:root {
    /** COLORES **/
    --blanco: #fff;
    --negro: #000;
    --gris: #e1e1e1;
    --amarillo: #ffce00;

    /** FUENTES **/
    --parrafo: "Open Sans", sans-serif;
    --heading: "PT Sans", sans-serif;
}



html {
    box-sizing: border-box;
    font-size: 62.5%;
}

*,
*::after,
*::before {
    box-sizing: inherit;
}

/** GLOBALES **/
body {
    width: 100vw;
    font-family: var(--parrafo);
    font-size: 1.6rem;
    line-height: 1.3;
}

.contenedor {
    width: min(90%, 130rem);
    margin: 0 auto;
}

a {
    text-decoration: none;
}

h1,
h2,
h3,
h4 {
    font-family: var(--heading);
}

h2 {
    font-size: 4rem;
}

h3 {
    font-size: 3.2rem;
}

h4 {
    font-size: 2.8rem;
}

img {
    max-width: 100%;
}

/** UTILIDADES **/
.no-margin {
    margin: 0;
}

.no-padding {
    padding: 0;
}

/** HEADER **/
.header {
    background-image: url(/assets/recursos/fondo2.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    height: 35rem;
}

.header--texto {
    text-align: center;
    color: var(--negro);
    margin-top: 0.5rem;
    line-height: 1.3;
}

@media (min-width: 768px) {
    .header {
        height: 42rem;
    }

    .header--texto {
        margin-top: 1rem;
    }

    .header-logo {
        margin: -30px;
    }
}

.barra {
    padding-top: 1rem;
    display: grid;
    grid-template-columns: 1fr 1fr;
}

@media (min-width: 768px) {
    .navegacion {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        padding: 0.5rem;
        margin: 0;
    }
}

.navegacion--enlace {
    display: block;
    text-align: center;
    font-size: 1.8rem;
    color: var(--negro);
    line-height: 1.6;
}


@media (min-width: 768px) {
    .contenido-principal {
        display: grid;
        grid-template-columns: 2fr 1fr;
        column-gap: 1rem;
    }
}

/** MAIN */
.pagina {
    max-width: 70rem;
    margin: 0 auto;
}

.entrada {
    border-bottom: 1px solid var(--gris);
    margin-bottom: 2rem;
}

.entrada:last-of-type {
    margin-bottom: 0;
}

.entrada--imagen img {
    border-radius: .5rem;
    width: 70rem;
}

.boton {
    display: block;
    font-family: var(--heading);
    color: var(--blanco);
    text-align: center;
    padding: 1rem 3rem;
    font-size: 1.6rem;
    text-transform: uppercase;
    font-weight: 700;
    margin-bottom: 2rem;
    border: none;
    border-radius: .5rem;
}

@media (min-width: 768px) {
    .boton {
        display: inline-block;
    }
}

.boton:hover {
    cursor: pointer;
}

.boton--primario {
    background-color: var(--negro);
}

.boton--primario:hover {
    opacity: 0.8;
}

.boton-secundario {
    background-color: #0c0052;
}

.boton-secundario:hover {
    background-color: #3b1edc;
}

/** aside **/
.ubicacion {
    list-style: none;
}

.sidebar h3 {
    margin-left: 4rem;
}

.widget-ubicacion {
    border-bottom: 1px solid var(--gris);
    margin-bottom: 2rem;
}

.widget-ubicacion_label {
    font-family: var(--heading);
    font-weight: 700;
}

.widget-ubicacion img {
    max-width: 100%;
    height: min(400, 600px);
    border-radius: 1rem;
}

@media (min-width: 768px) {
    .widget-ubicacion {
        width: 80%;
    }
}

/** FOOTER **/
.footer {
    background-color: #0c0052;
    padding: 0 0 1rem 0;
    margin: 0;
}

.contenedor--footer {
    padding-left: 2rem;
}

.barra--footer {
    margin: 0;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-content: center;
    align-items: center;
}

.footer-img {
    margin-left: 2rem;
}

.footer--text {
    color: var(--blanco);
    text-align: center;
    grid-column: 1 / 3;
    font-size: 1.5rem;
}

@media (min-width: 768px) {
    .navegacion--footer {
        display: flex;
        justify-content: space-around;
    }

    .footer--text {
        font-size: 1.8rem;
    }
}

.navegacion--footer .navegacion--enlace {
    font-size: 2rem;
    font-weight: bold;
    display: flex;
    justify-content: center;
    align-items: center;
}

.video {
    border-radius: 1rem;
    width: 100%;
    max-height: 100%;
}

.alt {
    height: 70rem;
}
@media (max-width: 760px){
    .navegacion--enlace-activo{
        background-color: var(--negro);
        padding: 0.5rem 0.5rem;
        text-align: center;
        color: var(--blanco);
        border-radius: 5px;
        font-weight: 700;
    }
}

/** Laptops **/
.contenido {
    width: min(97%, 78rem);
    margin: 0 auto;
}

.producto img {
    width: 100%;
}

.contenido h2 {
    text-align: center;
    font-family: var(--heading);
}

/** Grid **/
.grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
    margin: 0 auto 2rem;
}

.producto {
    padding: 0.3rem;
    box-shadow: 3px 4px 5px 0px rgba(0, 0, 0, 1);
    border-radius: 0.5rem;
    border: 1px solid var(--gris);
}

.producto-img {
    height: 37%;
}

@media (max-width: 760px) {
    .producto {
        height: 37.7rem;
    }

}

.producto-nombre {
    font-weight: bold;
    font-size: 2.5rem;
    text-align: center;
    margin-top: 0.5rem;
    margin-bottom: 0;
}

@media (max-width: 760px) {
    .producto-nombre {
        font-size: 1.5rem;
    }

    .caracteristicas {
        font-size: .9rem;
        margin-bottom: 0;
    }

    .caracteristicas li {
        margin-left: -2.5rem;
    }
}

.precio {
    margin-top: -1rem;
    text-align: center;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.precio--ahora,
.precio--antes,
.precio--oferta, .cuota {
    display: flex;
    flex-direction: column;
    line-height: 1;
}

.precio--ahora {
    color: #ffce00;
    font-weight: 900;
    font-size: 2rem;
    text-shadow: 0.1em 0.1em 0.2em black;
}

.informacion ul {
    margin-top: 1rem;
    margin-bottom: 1rem;
}

.ahora {
    font-size: 5.5rem;
}

.producto-img {
    height: 37%;
}

.antes, .cuota-monto {
    font-size: 2rem;
    text-decoration: line-through;
    text-decoration-color: red;
    font-weight: 700;
}
.cuota-monto{
    text-decoration: none;
}
.flex{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

@media (max-width: 760px) {
    .primero {
        grid-column: 1/3;
        height: 45rem;
        width: 26rem;
        margin: 0 auto;
    }

    .precio--ahora {
        font-size: 1rem;
    }

    .ahora {
        font-size: 3rem;
    }

    .ultimo {
        grid-column: 1 / 3;
        height: 40rem;
        justify-content: center;
        align-items: center;
    }

    .ultimo .producto-img {
        max-width: 90%;
        margin-left: 2rem;
    }

    .ultimo .informacion {
        display: grid;
        grid-template-columns: 3fr 1fr;
        justify-content: center;
        align-items: center;
    }

    .ultimo .producto-nombre {
        font-size: 2rem;
        margin-bottom: 0;
    }

    .ultimo ul {
        margin-left: 1rem;
        font-size: 1.5rem;
    }

    .ultimo .precio {
        display: flex;
        flex-direction: column;
    }
    .antes, .cuota-monto{
        font-size: 1.5rem;
    }
    .precio--antes,.cuota{
        font-size: 1rem;
        line-height: 1.2;
    }
}

.ultimo-gen {
    height: 100%;
}

.ultimo-gen .informacion {
    display: grid;
    justify-content: center;
    align-items: center;

}

.ultimo-gen .producto-nombre {
    font-size: 2rem;
    margin-bottom: 1rem;
}

.ultimo-gen ul {
    font-size: 1.5rem;
    margin-bottom: 2rem;
}

@media (max-width: 760px) {
    .ultimo-gen {
        height: 53rem;
    }

    .ultimo-gen .precio--ahora {
        font-size: 2rem;
    }

    .ultimo-gen .ahora {
        font-size: 6rem;
    }
}

@media (min-width: 768px) {
    .producto {
        width: 35rem;
        max-height: 60rem;
    }

    .ultimo-gen {
        width: 100%;
        max-height: 95vh;
    }

    .ultimo-gen .producto-img {
        height: 60%;
    }

    .ultimo-gen .informacion {
        display: grid;
        grid-template-columns: 1fr 1fr;
    }

    .font {
        font-size: 2.4rem;
    }

}


.doble {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.doble img {
    height: 35%;
    border-radius: 1rem;
}

.ultimo-gen {
    grid-column: -3 / -1;
}

@media (max-width: 500px) {
    .font {
        font-size: 1.2rem;
    }
}

.lap-img {
    width: 100%;
    height: 100%;
}

.normal {
    width: min(90%, 120rem);
    margin: 0 auto;
}

.producto-2 {
    width: 100%;
    margin: 0 auto 2rem;
    box-shadow: 3px 4px 5px 0px rgba(0, 0, 0, 1);
    border-radius: 0.5rem;
    border: 1px solid var(--gris);
}

.bold {
    font-weight: bold;
}

.precio--oferta {
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color: #fa0000;
    font-weight: 900;
    font-size: 3rem;
    text-shadow: 0.1em 0.1em 0.2em black;
    margin-top: 1.5rem;
}

.detalles {
    font-size: 1rem;
    margin-left: 2rem;
}

@media (max-width: 500px) {
    .precio--oferta {
        font-size: 1.7rem;
    }
    .up{
        margin-top: .7rem;
    }
    .detalles {
        font-size: 0.6rem;
        margin-left: 1rem;
        margin-top: -0.5rem;
    }
}

.logo-arriba {
    max-width: 120px;
    margin-top: -6rem;
    margin-bottom: -3rem;
    transform: rotate(30deg);
    z-index: -1;
}
.logo-abajo{
    position: relative;
    max-width: 120px;
    max-height: 100px;
    top: 1rem;
    margin-top: -7rem;
}
.subir{
    margin-top: -1.6rem;
}

@media (max-width: 500px) {
    .remove-bg{
        filter: brightness(1.1);
        mix-blend-mode: multiply;
        z-index: 2;
    }
    .logo-arriba {
        position: relative;
        max-width: 70px;
        max-height: 70px;
        top: 2rem;
        margin-left: 110px;
    }
    .logo-abajo {
        position: relative;
        max-width: 70px;
        max-height: 80px;
        top: 1rem;
        margin-top: -5rem;
    }
    .subir{
        margin-top: -1rem;
    }
}
@media (min-width: 600px){
    .logo-arriba{
        position: relative;
        top: 6rem;
        margin-left: 23.5rem;
    }
    .remove-bg{
        filter: brightness(1.1);
        mix-blend-mode: multiply;
        z-index: 2;
    }
}